@import url(https://fonts.googleapis.com/css2?family=Roboto&family=Roboto+Mono&display=swap);
.explorer__input {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.explorer__input-label {
  text-transform: capitalize;
  position: relative;
  display: inline-block;
  align-self: flex-start;
}

.explorer__input-label--required::before {
  content: "(required)";
  left: 106%;
  top: 0;
  position: absolute;
  text-transform: lowercase;
  font-size: 0.8rem;
  font-style: italic;
}

.explorer__input-field {
  padding: 8px;
  max-width: 300px;
  border-radius: 4px;
  border: 1px solid var(--neutral-300);
}

.explorer {
  max-width: 900px;
  margin: 0 auto 24px;
  background-color: var(--neutral-400);
  border-radius: 4px;
  border: 2px solid var(--neutral-300);
}

.explorer__content {
  padding: 1rem;
  display: none;
}

.explorer__content > * + * {
  margin-top: 1em;
}

.explorer__heading {
  display: flex;
  align-items: center;
  background-color: var(--neutral-500);
  padding: 8px;
  border-radius: 4px;
  cursor: pointer;
  position: relative;
}

.explorer__heading:focus {
  outline: none;
}

.explorer__heading::after {
  content: url(/api-explorer/static/media/chevron.929f649a.svg);
  position: absolute;
  top: 50%;
  height: 24px;
  width: 24px;
  right: 10px;
  transform: translateY(-50%);
  color: var(--neutral-200);
  transition: transform 0.25s ease;
}

.explorer--expanded .explorer__heading::after {
  transform: translateY(-50%) rotate(180deg);
}

.explorer--expanded .explorer__heading {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-bottom: 2px solid var(--neutral-300);
}
.explorer--expanded .explorer__content {
  display: block;
}

.explorer__method {
  color: var(--neutral-500);
  font-size: 10px;
  font-weight: bold;
  border-radius: 2px;
  display: inline-block;
  text-transform: uppercase;
  height: 25px;
  width: 50px;
  text-align: center;
  margin-right: 8px;
  background-color: #2980b9;
}

.explorer__method--post {
  background-color: #2980b9;
}
.explorer__method--get {
  background-color: #27ae60;
}
.explorer__method--delete {
  background-color: #f34712;
}
.explorer__method--put {
  background-color: #f39c12;
}

.explorer__title {
  font-size: 1.5rem;
  line-height: 30px;
  font-weight: bold;
  color: var(--neutral-100);
}

.explorer__request-url {
  background-color: var(--neutral-500);
  border-radius: 4px;
}

.explorer__request-url code {
  display: inline-block;
  padding: 8px;
}

.explorer__request-url code:first-child {
  border-right: 2px solid var(--neutral-400);
}

.explorer__section {
  font-size: 0.8rem;
  display: grid;
  grid-template-areas:
    "header header"
    "name description"
    "key value";
  grid-template-columns: 1fr 5fr;
  grid-gap: 24px;
  margin-top: 24px;
}

.explorer__section-header {
  grid-area: header;
  font-size: 1rem;
}

.explorer__section-name {
  grid-area: name;
  border-bottom: 1px solid var(--neutral-300);
  font-weight: bold;
}

.explorer__section-description {
  grid-area: description;
  border-bottom: 1px solid var(--neutral-300);
  font-weight: bold;
}

.explorer__request-param-name {
  grid-area: key;
}

.explorer__request-param-value {
  grid-area: value;
}

.explorer__actions button:first-child {
  margin: 0 8px 8px 0;
}

.explorer__response-status {
  grid-area: key;
}

.explorer__response-status code {
  font-size: 0.8rem;
}

.explorer__response-body {
  grid-area: value;
}

.explorer__response-body pre {
  background-color: var(--neutral-500);
  padding: 10px;
  border-radius: 4px;
  margin-top: 8px;
}

:root {
  --primary-100: #00819d;
  --primary-200: #005668;
  --neutral-100: #070a2c;
  --neutral-200: rgba(7, 10, 44, 0.7);
  --neutral-300: #dddddd;
  --neutral-400: #f2f5fa;
  --neutral-500: #ffffff;

  --primary-font: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;
  --code-font: "Roboto Mono", monospace;
}

html {
  font-size: 16px;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-family: var(--primary-font);
  color: rgba(7, 10, 44, 0.7);
  color: var(--neutral-200);
  line-height: 25px;
  box-sizing: border-box;
  padding: 24px;
}

h1 {
  font-size: 2rem;
  max-width: 900px;
  margin: 1em auto;
}

button {
  font-family: var(--body-font);
  background-color: #00819d;
  background-color: var(--primary-100);
  border: 1px solid #00819d;
  border: 1px solid var(--primary-100);
  color: #ffffff;
  color: var(--neutral-500);
  transition: background-color 0.25s ease, border 0.25s ease;
  font-size: 1rem;
  line-height: 24px;
  font-weight: bold;
  padding: 8px 16px;
  border-radius: 4px;
  cursor: pointer;
  position: relative;
}

button:hover {
  background-color: #005668;
  background-color: var(--primary-200);
  border: 1px solid #005668;
  border: 1px solid var(--primary-200);
}

button.btn-secondary {
  background-color: transparent;
  border: 1px solid #00819d;
  border: 1px solid var(--primary-100);
  color: #00819d;
  color: var(--primary-100);
}

button.btn-secondary:hover {
  border: 1px solid #005668;
  border: 1px solid var(--primary-200);
  color: #005668;
  color: var(--primary-200);
}

input {
  font-family: var(--body-font);
  width: 100%;
  font-size: 1rem;
}

input.invalid {
  border: 1px solid red;
  -webkit-animation: invalidInput 0.25s;
          animation: invalidInput 0.25s;
}

code,
pre {
  font-family: "Roboto Mono", monospace;
  font-family: var(--code-font);
  font-size: 0.9rem;
  line-height: 20px;
}

@-webkit-keyframes invalidInput {
  25% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(5px);
  }
  75% {
    transform: translateX(-5px);
  }
}

@keyframes invalidInput {
  25% {
    transform: translateX(-5px);
  }
  50% {
    transform: translateX(5px);
  }
  75% {
    transform: translateX(-5px);
  }
}

@media (max-width: 400px) {
  html {
    font-size: 14px;
  }

  code,
  pre {
    font-size: 0.8rem;
  }
}

